.tabs {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.tab {
  padding: 10px 20px;
  border: none;
  background-color: #ddd;
  color: #333;
  cursor: pointer;
  margin: 0 5px;
  transition: background-color 0.3s, color 0.3s;
}

.tab.active {
  background-color: #6200ea;
  color: white;
}

.tab:hover {
  background-color: #bbb; /* Estilo para cuando el botón es hovered */
}
